.ysConfirm .ys_layer {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.5) none repeat scroll 0 0;
	z-index: 2147000000
}

.ysConfirm .popBox {
	width: 354px;
	height: 230px;
	box-shadow: 2px 2px 5px #999;
	border-radius: 8px;
	border: 1px solid #bebebe;
	color: #666;
	background: #fff;
	left: 40%;
	top: 25%;
	position: fixed;
	z-index: 2147000000
}

.ysConfirm .popBox .ttBox {
	width: 352px;
	height: 28px;
	line-height: 28px;
	text-indent: 8px;
	font-size: 14px;
	color: #666;
	background: #eee;
	border-radius: 8px 8px 0 0;
	margin-bottom: 25px
}

.ysConfirm .popBox .clsBtn {
	width: 10px;
	height: 10px;
	display: inline-block;
	background: url(../images/xtj/pc_01.png) no-repeat;
	float: right;
	margin: 10px 10px 0 0;
	cursor: pointer
}

.ysConfirm .popBox .txtBox {
	margin-left: 70px;
	margin-right: 50px;
	margin-bottom: 10px;
	font-size: 14px
}

.ysConfirm .popBox .txtBox img {
	float: left;
	margin-top: 3px;
	margin-left: -24px;
	width: 16px;
	height: 16px
}

.ysConfirm .popBox .txtBox p {
	margin-top: 16px;
	line-height: 26px;
	overflow-x: hidden;
	overflow-y: auto;
	font-size: 14px
}

.ysConfirm .popBox .btnArea {
	text-align: center
}

.ysConfirm .popBox .btnGroup {
	margin-right: auto;
	margin-left: auto
}

.ysConfirm .popBox .sgBtn {
	border-radius: 5px;
	border: 1px solid #bebebe;
	width: 84px;
	height: 24px;
	text-align: center;
	font-size: 12px;
	display: inline-block;
	line-height: 22px;
	cursor: pointer;
	margin-left: 15px;
	margin-top: 8px
}

.ysConfirm .popBox .sgBtn.ok {
	color: #0095d9
}

.ysConfirm .popBox .sgBtn.cancel {
	color: #b6b6b6
}

.ysConfirm .popBox .sgBtn-bottom {
	line-height: 20px;
	color: #b6b6b6;
	border-top: 1px solid #bebebe;
	margin-top: 22px
}

.ysConfirm .popBox .sgBtn-bottom p {
	margin-left: 20px;
	margin-right: 20px;
	font-size: 12px;
	font-family: '宋体';
	margin-top: 10px
}

.ysLogin .ys_layer {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.5) none repeat scroll 0 0;
	z-index: 2147000000
}

.ysLogin .popBox {
	width: 460px;
	height: 376px;
	background: #fff;
	position: fixed;
	top: 30%;
	left: 39%;
	z-index: 2147000000;
	box-shadow: 0 0 5px #000
}

.ysLogin .popBox h2 {
	height: 70px;
	line-height: 70px;
	border-bottom: #acabab solid 1px;
	color: #000;
	font: 24px/70px '微软雅黑';
	text-align: center;
	position: relative
}

.ysLogin .popBox h2 i {
	display: block;
	width: 26px;
	height: 26px;
	background: url(../images/auction/close10.png) no-repeat top center;
	position: absolute;
	top: 25px;
	right: 25px;
	cursor: pointer;
	background-size: 100%
}

.ysLogin .popBox ul {
	padding: 27px 65px 0
}

.ysLogin .popBox li {
	font: 16px/50px '微软雅黑';
	color: #666
}

.ysLogin .popBox li input {
	height: 40px;
	line-height: 40px;
	color: #ababab;
	font: 14px/40px '微软雅黑';
	padding-left: 7px;
	border: #ddd solid 1px;
	background: #fff
}

.ysLogin .popBox li .inp1 {
	width: 263px;
	text-indent: 20px;
	color: #333
}

.ysLogin .popBox li b {
	color: red;
	vertical-align: middle;
	padding-right: 3px
}

.ysLogin .popBox .btn {
	width: 263px;
	height: 40px;
	line-height: 40px;
	color: #fff;
	font: 16px/40px '微软雅黑';
	text-align: center;
	background: #22ac38;
	border: none;
	cursor: pointer;
	margin-top: 19px;
	margin-left: 59px
}

.ysLogin .popBox p {
	color: #000;
	font: 14px/24px '宋体';
	padding: 21px 40px 0;
	text-align: center
}

.ysLogin .popBox p span {
	color: #888
}

.toper .footprint {
	background: transparent url(/Content/images/footprint_icon.png) no-repeat scroll 0 center;
	padding-left: 16px
}

.toper a {
	color: #6d6e71;
	display: inline-block;
	margin: 0 5px;
	text-decoration: none
}

.consultation {
	position: fixed;
	width: 93px;
	height: auto;
	bottom: 8px;
	right: 41px;
	font-family: "微软雅黑","Microsoft YaHei",Arial,"黑体","宋体",sans-serif;
	z-index: 999
}

.consultation_left {
	position: fixed;
	width: 93px;
	height: auto;
	bottom: 8px;
	left: 8px;
	font-family: "微软雅黑","Microsoft YaHei",Arial,"黑体","宋体",sans-serif;
	z-index: 999
}

.consultation .consultation-title,.consultation_left .consultation-title {
	width: 93px;
	height: auto
}

.consultation-title .consultation-font {
	position: relative;
	width: 80px;
	height: 28px;
	background-color: #d74a25;
	font-size: 14px;
	color: #fff;
	line-height: 28px;
	padding-left: 7px;
	margin-top: 27px;
	border-radius: 3px
}
.consultation-title .consultation-img {
	position: absolute;
	width: 60px;
	height: 60px;
	margin-left: 62px;
	top: 0
}

.consultation .qq,.consultation_left .qq {
	position: relative;
	width: 93px;
	height: 151px;
	margin-top: 8px
}
